import { Component, Element, Event, EventEmitter, Listen, Prop, h } from '@stencil/core';
import { RADIO_BUTTON_VARIANTS } from './export';

/**
 * @slot description - Slot for the description
 */
@Component({
  tag: 'mdx-radio-button',
  styleUrl: 'radio-button.scss',
  shadow: true,
})
export class RadioButton {
  @Element()
  host: HTMLMdxRadioButtonElement;

  @Listen('selectRadioButton', { target: 'body' })
  handleSelectRadioButton({ detail }: CustomEvent<{ checked: boolean; name?: string; value?: string }>) {
    if (
      !!this.name &&
      detail.name === this.name &&
      !this.disabled &&
      detail.checked &&
      this.checked &&
      this.value !== detail.value
    ) {
      this.checked = false;
    }
  }

  @Event()
  selectRadioButton: EventEmitter<{ checked: boolean; name?: string; value?: string }>;

  @Prop({ reflect: true })
  variant: (typeof RADIO_BUTTON_VARIANTS)[keyof typeof RADIO_BUTTON_VARIANTS] = RADIO_BUTTON_VARIANTS.WITH_BORDER;

  @Prop()
  label!: string;

  @Prop()
  name?: string;

  @Prop()
  value?: string;

  @Prop()
  error?: string;

  @Prop()
  radioButtonId!: string;

  @Prop({ reflect: true })
  checked = false;

  @Prop()
  disabled = false;

  render() {
    return (
      <div class="radio-button">
        <label class="radio-button__label">
          <div class="radio-button__wrapper">
            <input
              class="radio-button__input"
              type="radio"
              value={this.value}
              name={this.name}
              disabled={this.disabled}
              checked={this.checked}
              onChange={(event) => {
                this.checked = (event.target as HTMLInputElement).checked;
                this.selectRadioButton.emit({ checked: this.checked, name: this.name, value: this.value });
              }}
            />
            <span class="radio-button__label-text">{this.label}</span>
          </div>
          {this.host.querySelector('[slot="description"]') && (
            <div class="radio-button__description">
              <slot name="description" />
            </div>
          )}
        </label>
        {this.error && <mdx-error-hint class="radio-button__error-message" text={this.error} />}
      </div>
    );
  }
}
