:host .radio-button {
  box-sizing: border-box;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  font: var(--mdx-comp-radiobutton-font-label);
  padding: var(--mdx-comp-radiobutton-padding-vertical-default) var(--mdx-comp-radiobutton-padding-horizontal-default)
    var(--mdx-comp-radiobutton-padding-vertical-default) var(--mdx-comp-radiobutton-padding-horizontal-default);

  &__wrapper {
    display: flex;
    align-items: center;
    height: var(--mdx-comp-radiobutton-font-label-line-height);
  }

  &__label {
    transition: var(--mdx-comp-checkbox-transition-hover-fade-in-out);
  }

  &__label-text {
    padding-left: var(--mdx-comp-radiobutton-gap-box-label);
  }

  &__input {
    cursor: pointer;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-style: solid;
    border-radius: 100%;
    border-width: var(--mdx-comp-radiobutton-border-width-default);
    border-color: var(--mdx-comp-radiobutton-unchecked-input-border-default);
    min-width: var(--mdx-comp-radiobutton-sizing-ellipse);
    min-height: var(--mdx-comp-radiobutton-sizing-ellipse);
    background-color: var(--mdx-comp-radiobutton-unchecked-input-background-default);
    margin: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;

    &:hover {
      background-color: var(--mdx-comp-radiobutton-unchecked-input-background-hover);
    }

    &::before {
      content: '';
      border-radius: 100%;
      transition: var(--mdx-comp-transition-fade-radio-circle-in-out);
    }
  }

  &__description {
    font: var(--mdx-comp-radiobutton-font-description);
    color: var(--mdx-comp-radiobutton-unchecked-description-color-default);
    margin-top: var(--mdx-comp-radiobutton-gap-label-description);
    padding-left: calc(var(--mdx-comp-radiobutton-sizing-ellipse) + var(--mdx-comp-radiobutton-gap-box-label));

    &:hover {
      color: var(--mdx-comp-radiobutton-unchecked-description-color-hover);
    }
  }
}

:host([variant='border']) .radio-button {
  &__label {
    cursor: pointer;
    border-style: solid;
    border-width: var(--mdx-comp-radiobutton-border-width-default);
    border-color: var(--mdx-comp-radiobutton-unchecked-border-color-default);
    border-radius: var(--mdx-comp-radiobutton-border-radius-default);
    background-color: var(--mdx-comp-radiobutton-unchecked-background-color-default);
    color: var(--mdx-comp-radiobutton-unchecked-label-color-default);
    padding: var(--mdx-comp-radiobutton-padding-vertical-default) var(--mdx-comp-radiobutton-padding-horizontal-default);

    &:hover {
      background-color: var(--mdx-comp-radiobutton-unchecked-background-color-hover);
      border-color: var(--mdx-comp-radiobutton-unchecked-border-color-hover);
      border-width: var(--mdx-comp-radiobutton-border-width-hover);
    }
  }
}

:host([variant='no-border']) .radio-button {
  &__label {
    cursor: pointer;
    background-color: var(--mdx-comp-radiobutton-unchecked-background-color-default);
    color: var(--mdx-comp-radiobutton-unchecked-label-color-default);
    padding: var(--mdx-comp-radiobutton-padding-vertical-default) var(--mdx-comp-radiobutton-padding-horizontal-default)
      var(--mdx-comp-radiobutton-padding-vertical-default) 0;

    &:hover {
      background-color: var(--mdx-comp-radiobutton-unchecked-background-color-hover);
      border-radius: var(--mdx-comp-radiobutton-border-radius-default);
      border-width: var(--mdx-comp-radiobutton-border-width-hover);
      color: var(--mdx-comp-radiobutton-unchecked-label-color-hover);
    }
  }
}

:host([checked]) .radio-button {
  &__label {
    background-color: var(--mdx-comp-radiobutton-checked-background-color-default);
    border-color: var(--mdx-comp-radiobutton-checked-border-color-default);
    border-width: var(--mdx-comp-radiobutton-border-width-default);
    color: var(--mdx-comp-radiobutton-checked-label-color-default);

    &:hover {
      background-color: var(--mdx-comp-radiobutton-checked-background-color-hover);
      border-color: var(--mdx-comp-radiobutton-checked-border-color-hover);
      border-width: var(--mdx-comp-radiobutton-border-width-hover);
      color: var(--mdx-comp-radiobutton-checked-label-color-default);
    }
  }

  &__input {
    border-color: var(--mdx-comp-radiobutton-checked-input-border-default);
    background-color: var(--mdx-comp-radiobutton-checked-input-background-default);

    &:hover {
      background-color: var(--mdx-comp-radiobutton-checked-input-background-hover);
    }

    &:checked::before {
      border-radius: 100%;
      min-width: var(--mdx-comp-radiobutton-sizing-ellipse-filled);
      min-height: var(--mdx-comp-radiobutton-sizing-ellipse-filled);
      border-color: var(--mdx-comp-radiobutton-checked-input-border-default);
      background-color: var(--mdx-comp-radiobutton-checked-input-color-default);
      content: '';
    }
  }

  &__description {
    color: var(--mdx-comp-radiobutton-checked-description-color-default);

    &:hover {
      color: var(--mdx-comp-radiobutton-checked-description-color-hover);
    }
  }
}

:host([disabled]) .radio-button {
  &__label {
    background-color: var(--mdx-comp-radiobutton-unchecked-background-color-disabled);
    border-color: var(--mdx-comp-radiobutton-unchecked-border-color-disabled);
    border-width: var(--mdx-comp-radiobutton-border-width-disabled);
    color: var(--mdx-comp-radiobutton-unchecked-label-color-disabled);

    &:hover {
      background-color: var(--mdx-comp-radiobutton-unchecked-background-color-disabled);
      border-color: var(--mdx-comp-radiobutton-unchecked-border-color-disabled);
      border-width: var(--mdx-comp-radiobutton-border-width-disabled);
      color: var(--mdx-comp-radiobutton-unchecked-label-color-disabled);
    }
  }

  &__input {
    border-color: var(--mdx-comp-radiobutton-unchecked-input-border-disabled);
    background-color: var(--mdx-comp-radiobutton-unchecked-input-background-disabled);
    border-width: var(--mdx-comp-radiobutton-border-width-disabled);

    &:focus-visible {
      outline-style: solid;
      border-radius: var(--mdx-comp-keyboard-focus-border-radius-default);
      outline-width: var(--mdx-comp-keyboard-focus-border-width-default);
      outline-color: var(--mdx-comp-keyboard-focus-color-default);
    }
  }

  &__description {
    color: var(--mdx-comp-radiobutton-unchecked-description-color-disabled);
  }
}

:host([disabled][checked]) .radio-button {
  &__label {
    background-color: var(--mdx-comp-radiobutton-checked-background-color-disabled);
    border-color: var(--mdx-comp-radiobutton-checked-border-color-disabled);
    border-width: var(--mdx-comp-radiobutton-border-width-disabled);
    color: var(--mdx-comp-radiobutton-unchecked-label-color-disabled);

    &:hover {
      background-color: var(--mdx-comp-radiobutton-checked-background-color-default);
      border-color: var(--mdx-comp-radiobutton-checked-border-color-disabled);
      border-width: var(--mdx-comp-radiobutton-border-width-disabled);
      color: var(--mdx-comp-radiobutton-checked-label-color-disabled);
    }
  }

  &__input {
    border-color: var(--mdx-comp-radiobutton-checked-input-border-disabled);
    background-color: var(--mdx-comp-radiobutton-checked-input-background-disabled);
    border-width: var(--mdx-comp-radiobutton-border-width-disabled);

    &:disabled::before {
      background-color: var(--mdx-comp-radiobutton-checked-input-color-disabled);
      border-color: var(--mdx-comp-radiobutton-checked-input-border-disabled);
    }
  }

  &__description {
    color: var(--mdx-comp-radiobutton-checked-description-color-disabled);
  }
}

:host([error]) .radio-button {
  &__error-message {
    margin-top: var(--mdx-comp-checkbox-gap-container-error);
  }
}
